:root {
  --primary-color: #49be65;
  --secondary-color: #6bca81;
  --shadow-color: #e2f7e7;
  --whiten-color: #f5fef7;
  --darken-color: #3a9b51;
}
::selection {
  background-color: #e2f7e7;
  background-color: var(--shadow-color);
  color: #3a9b51;
  color: var(--darken-color);
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: #70d287;
  border-radius: 8px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #3a9b51;
  background-color: var(--darken-color);
}
@-webkit-keyframes onStart {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@keyframes onStart {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
}
* {
  font-family: "Inter", sans-serif;
  box-sizing: border-box;
}
h1 {
  font-family: "Poppins", sans-serif;
}
.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  max-height: 100%;
  margin: 0;
}
.hidden {
  visibility: hidden;
}

.likeButton {
  display: inline-block;
  text-decoration: none;
}

button,
.likeButton {
  background-color: #49be65;
  background-color: var(--primary-color);
  color: white;
  padding: 10px 14px;
  border: 0;
  font-size: 1.1rem;
  border-radius: 8px;
  box-shadow: 0 0 20px -4px #6bca81;
  box-shadow: 0 0 20px -4px var(--secondary-color);
  transition: 0.2s;
  cursor: pointer;
  text-align: center;
}
button:hover,
.likeButton:hover {
  background-color: #3a9b51;
  background-color: var(--darken-color);
}
button:active,
.likeButton:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
.disabledButton {
  background-color: rgb(194, 194, 194);
  box-shadow: 0 0 20px rgb(210, 210, 210);
  opacity: 0.9;
  cursor: not-allowed;
}

.disabledButton:hover {
  background-color: rgb(150, 150, 150);
}

.disabledButton:active {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 0.7;
}

.smallButton {
  padding: 6px 10px;
  font-size: 1rem;
}

.dangerButton {
  background-color: rgb(235, 78, 78);
  box-shadow: 0 0 20px -4px rgba(235, 78, 78, 0.621);
}
.dangerButton:hover {
  background-color: rgb(164, 45, 45);
}

.infoButton {
  background-color: white;
  box-shadow: none;
  border: 1px solid rgb(239, 239, 239);
  color: rgb(101, 101, 101);
}
.infoButton:hover {
  color: black;
  background-color: white;
  box-shadow: 0 0 10px rgb(244, 244, 244);
}

@media screen and (max-width: 1000px) {
  .container {
    grid-template-columns: 1fr;
  }
  .searchComponent {
    height: 100vh;
  }
  .smallButton {
    padding: 6px 10px;
    font-size: 0.8rem;
  }
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.spinning {
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.label {
  display: inline-block;
  background-color: #e2f7e7;
  background-color: var(--shadow-color);
  padding: 5px 10px;
  color: #49be65;
  color: var(--primary-color);
  border-radius: 6px;
  margin-right: 10px;
}

.searchComponent {
  border-left: 2px solid rgb(245, 245, 245);
  background: linear-gradient(to right, #fcfcfc, #f7f7f7);
  padding: 25px;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
}
.searchComponent__searchBox {
  display: flex;
  padding: 20px;
  padding-bottom: 0;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
}
.searchComponent__input {
  padding: 15px 20px;
  font-size: 1.1rem;
  width: 100%;
  border: 1px solid rgb(232, 232, 232);
  border-radius: 6px;
  transition: 0.2s;
  margin: 50px 0;
}
.searchComponent__input::-webkit-input-placeholder {
  color: rgb(206, 206, 206);
}
.searchComponent__input:-ms-input-placeholder {
  color: rgb(206, 206, 206);
}
.searchComponent__input::placeholder {
  color: rgb(206, 206, 206);
}
.searchComponent__input:hover {
  border: 1px solid rgb(175, 175, 175);
}
.searchComponent__input:focus {
  outline: none;
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 20px var(--shadow-color);
}

.searchComponent__trackingButton {
  display: flex;
  align-items: center;
  margin-right: auto;
  margin-top: 40px;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  left: 0;
}
.searchComponent__trackingWarning {
  margin-left: 12px;
  color: grey;
  -webkit-filter: drop-shadow(0 0 15px black);
          filter: drop-shadow(0 0 15px black);
}

.searchComponent__info {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: rgb(207, 207, 207);
  padding: 50px;
  text-align: center;
}
.searchComponent__icon {
  font-size: 3rem;
}
.searchComponent__textInfo {
  margin: 10px;
  font-size: 1.4rem;
  font-weight: 400;
}
@media screen and (max-width: 1000px) {
  .searchComponent__searchBox {
    padding: 0;
  }
  .searchComponent {
    overflow-y: visible;
    overflow-x: visible;
  }
  .searchComponent__trackingButton {
    bottom: 20px;
    font-size: 1rem !important;
  }
}

.mainNav {
  display: flex;
  flex-direction: row-reverse;
  font-size: 2rem;
}
.mainNav > * {
  opacity: 0.5;
  transition: 0.15s;
  cursor: pointer;
}
.mainNav > *:hover {
  opacity: 1;
}
.nav__settingsButton {
  display: block;
  color: black;
}
.nav__settingsButton:hover {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.SearchResult__element {
  background-color: white;
  width: 100%;
  padding: 25px;
  border: 1px solid rgb(232, 232, 232);
  border-radius: 6px;
  transition: 0.2s border, box-shadow;
  box-shadow: 0 0 20px rgba(199, 199, 199, 0.155);
}
.SearchResult__element:hover {
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 20px var(--shadow-color);
}
.SearchResult__box {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}
.SearchResult__iteminfo--stickyContainer {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.SearchResult__iteminfo {
  position: relative;
  max-width: 40%;
}
.SearchResult__iteminfo--priceLabel {
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgb(232, 248, 233);
  color: rgb(46, 129, 75);
  -webkit-clip-path: polygon(15% 0%, 100% 0%, 100% 100%, 15% 100%, 0 50%);
          clip-path: polygon(15% 0%, 100% 0%, 100% 100%, 15% 100%, 0 50%);
  font-weight: 500;
  padding: 8px 14px;
  padding-left: 25px;
  font-size: 1.1rem;
  border-right: 2px solid var(--primary-color);
}
.SearchResult__title {
  margin: 0;
  font-size: 1.5rem;
  color: #3c6144;
}
.SearchResult__desc {
  margin: 4px 0;
  font-size: 1.15rem;
  font-weight: 500;
  color: rgb(135, 135, 135);
}
.SearchResult__sku {
  margin: 0;
  margin-top: 20px;
  font-size: 1rem;
  font-weight: 500;
  color: rgb(191, 191, 191);
}
.SearchResult__desc--seller {
  display: inline-block;
  background-color: var(--shadow-color);
  padding: 5px 10px;
  color: var(--primary-color);
  border-radius: 6px;
}
.SearchResult__sales {
  width: 100%;
}
.SearchResult__table {
  width: 100%;
  text-align: center;
  background-color: #fdfdfd;
  border: 1px solid rgb(233, 233, 233);
  box-shadow: 0 0 20px #f6f6f6;
  padding: 10px;
  border-radius: 8px;
  border-spacing: 0px;
}

.SearchResult__table td {
  padding: 7px 0;
  color: rgb(98, 98, 98);
  transition: 0.05s;
  cursor: pointer;
  border: 1px solid transparent;
  border-top: 1px solid rgb(235, 235, 235);
}

.SearchResult__table tr:hover td {
  background-color: rgb(244, 244, 244);
  color: rgb(16, 16, 16);
}

.SearchResult__table th {
  font-size: 1.1rem;
  padding: 10px 0;
  font-weight: 600;
  color: var(--darken-color);
  background-color: var(--shadow-color);
}

tr:first-child th:first-child {
  border-top-left-radius: 4px;
}
tr:first-child th:last-child {
  border-top-right-radius: 4px;
}

.SearchResult__table--header {
  border-radius: 4px;
  position: -webkit-sticky;
  position: sticky;
  box-shadow: 0 0 10px var(--shadow-color);
  top: 0;
}

.selectedSize td {
  background-color: var(--whiten-color);
  border-top: 1px solid var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  box-shadow: 0 0 10px var(--shadow-color);
}
.selectedSize > td {
  color: var(--darken-color);
  font-weight: 900;
}
.selectedSize:hover td {
  background-color: var(--whiten-color) !important;
  color: var(--darken-color) !important;
}
.SearchResult__sizeColumn {
  display: flex;
  justify-content: space-evenly;
}

@media screen and (max-width: 1750px) {
  .SearchResult__box {
    flex-direction: column;
  }
  .SearchResult__iteminfo {
    max-width: 100%;
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .SearchResult__box {
    flex-direction: column;
  }
}

.trackComponent {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  align-content: start;
  overflow-y: scroll;
}
.trackComponent__empty {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(207, 207, 207);
  padding: 100px;
}
.trackComponent__missingIcon {
  font-size: 4rem;
}
.trackComponent__missingTitle {
  margin: 10px;
  font-size: 1.6rem;
}
.trackComponent__missingDesc {
  margin: 10px;
  font-size: 1.2rem;
  font-weight: 400;
}
.trackComponent__itemCard {
  position: relative;
  border: 1px solid rgb(227, 227, 227);
  padding: 5px 20px 20px 20px;
  height: 100%;
  border-radius: 6px;
  transition: 0.2s;
  cursor: pointer;
  text-decoration: none;
}
.trackComponent__itemCard:hover {
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 20px var(--shadow-color);
  /* background-color: rgb(250, 250, 250); */
  background-color: var(--whiten-color);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.itemCard__image {
  max-width: 100%;
  mix-blend-mode: darken;
}
.itemCard__title {
  margin: 0;
  color: #3c6144;
  font-size: 1.2rem;
  line-height: 1.3rem;
}
.itemCard__colorway {
  margin: 2px 0;
  font-size: 1rem;
  font-weight: 500;
  color: rgb(135, 135, 135);
}
.itemCard__sku {
  margin: 0;
  /* margin-top: 0; */
  font-size: 1rem;
  font-weight: 500;
  color: rgb(191, 191, 191);
}

.itemCard__sizeTracking {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: rgb(232, 248, 233);
  color: rgb(46, 129, 75);
  font-weight: 500;
  padding: 8px 14px;
  border-radius: 6px;
  font-size: 0.9rem;
}
.trackComponent__emptyList {
  width: 100%;
  text-align: center;
}

@media screen and (max-width: 1000px) {
  .trackComponent {
    overflow-y: visible;
    overflow-x: visible;
  }
}
@media screen and (max-width: 1400px) {
  .trackComponent {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 600px) {
  .trackComponent {
    grid-template-columns: 1fr;
  }
}

.settings__container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.29);
  box-shadow: 0 0 50px 50px rgba(0, 0, 0, 0.296);
  /* animation */
  -webkit-animation-name: onStart;
          animation-name: onStart;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.settings__box {
  background-color: white;
  padding: 40px;
  border-radius: 8px;
  border: 1px solid rgb(237, 237, 237);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.067);
  width: 50vw;
}
.settings__titleBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 30px 10px;
}
.settings__title {
  margin: 0;
  font-size: 1.8rem;
  color: #3c6144;
}
.settings__closeButton {
  font-size: 1.5rem;
  opacity: 0.5;
  background-color: white;
  padding: 10px 12px;
  border-radius: 100px;
  cursor: pointer;
  transition: 0.1s;
}
.settings__closeButton:hover {
  opacity: 1;
  background-color: rgb(240, 240, 240);
}
.settings__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}
.settings__list > li {
  display: flex;
  align-items: center;
  padding: 15px 15px;
  color: #35563d;
  font-size: 1rem;
  transition: 0.1s;
  border-radius: 6px;
}
.settings__list > li:hover {
  background-color: #f8f8f8;
}
.settings__list > li:not(:last-child) {
  border-bottom: 1px solid rgb(245, 245, 245);
}
.settings__list > li > p {
  margin: 0;
}
.settings__list > li > button,
.settings__list .likeButton {
  margin-left: auto;
}

.moreInfo {
  opacity: 0.5;
  cursor: pointer;
  margin: 0 5px;
  font-size: 1.2rem;
}
.moreInfo:hover {
  opacity: 1;
}

.apiinformation {
  line-height: 1.5rem;
  color: black;
}
.apiinformation:hover {
  background-color: white !important;
}
.apiinformation a {
  color: var(--primary-color);
}

@media screen and (max-width: 1000px) {
  .settings__box {
    width: 90vw;
  }
  .settings__container {
    position: fixed;
    top: 0;
  }
  .settings__list > li {
    display: flex;
    font-size: 0.8rem;
    padding: 15px 15px;
  }
}

.priceLabel {
