.mainNav {
  display: flex;
  flex-direction: row-reverse;
  font-size: 2rem;
}
.mainNav > * {
  opacity: 0.5;
  transition: 0.15s;
  cursor: pointer;
}
.mainNav > *:hover {
  opacity: 1;
}
.nav__settingsButton {
  display: block;
  color: black;
}
.nav__settingsButton:hover {
  transform: rotate(30deg);
}
